






















.addmovie {
    width: 120px;
    height: 150px;
    border: 1px dashed  #9e9d9d;
    border-radius: 6px;
    margin: 10px 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    & i {
        display: inherit;
    }
}

@primary-color: #1b1b1b;@item-hover-bg: #ddd;@border-radius-base: 2px;@input-hover-border-color: #0d499e;